import React from 'react';
import './index.css'
import { getHistory, setHistory } from '../../services/auth';
import { withRouter } from 'react-router';
import Unverified from '../Account/Unverified'
import { Table } from 'reactstrap';


const expired = "data:image/png;base64,";
const original = "data:image/png;base64,";


const invalidQR = 'data:image/png;base64,';

class History extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      image: "",
      name: "",
      expiry: "",
      nafdac: "",
      license: "",
      contact: "",
      notification: '',
      history: [],
      falseQR: false
    }
    this.checkExpired = this.checkExpired.bind(this);
    this.commonState = this.commonState.bind(this);
    this.tryParseJSON = this.tryParseJSON.bind(this)
  }

  commonState(obj) {
    let d = new Date();
    let image = original; let exp = '';
    if (obj.expiry !== '') {
      exp = this.checkExpired(obj.expiry);
    }
    if (exp === 'expired') {
      image = expired;
    }
    this.setState({
      name: obj.name,
      expiry: obj.expiry.replace(/\-/g, '/'),
      image,
      license: obj.license,
      falseQR: false,
      contact: obj.contact,
      nafdac: obj.nafdac
    })
    setHistory(JSON.stringify(obj));
  }

  checkExpired(data) {
    return new Date() > new Date(data) ? "expired" : "fresh";
  }

  tryParseJSON(jsonString) {
    try {
      var o = JSON.parse(jsonString);
      if (o && typeof o === "object") {
        return true;
      }
    } catch (e) { }
    return false;
  };

  componentDidMount() {
    let fromScan = this.props.location;
    if (fromScan.json === undefined) {
      this.setState({
        notification: "No Data. Please go back to scan"
      })
    } else {
      if (fromScan.json.valid) {
        this.commonState(fromScan.json.data);
      } else {
        this.setState({
          image: invalidQR,
          falseQR: true,
          notification: "INVALID QR CODE"
        });
      }
    }
  }

  render() {
    const { name, falseQR, image, expiry, license, contact, nafdac, notification, history } = this.state;
    return (
      <div className="historyPage">

        <div className="productInfo">
          {name === '' ? "" :
            <div>
              <img className="detailImage" src={image} />
              <h3 className="dTail">
                Details
              </h3>
              <div className="fxDetail">
                <div className="pTitle">NAME:</div>
                <div className="pDetail">{name}</div>
              </div>
            </div>
          }
          {expiry === '' ? "" :
            <div className="fxDetail">
              <div className="pTitle">EXPIRY DATE:</div>
              <div className="pDetail">{expiry}</div>
            </div>
          }
          {nafdac === '' ? "" :
            <div className="fxDetail">
              <div className="pTitle">NAFDAC:</div>
              <div className="pDetail">{nafdac}</div>
            </div>
          }

          {license === '' ? "" :
            <div className="fxDetail">
              <div className="pTitle">LICENSE:</div>
              <div className="pDetail">{license}</div>
            </div>
          }
          {contact === '' ? "" :
            <div className="fxDetail">
              <div className="pTitle">CONTACT:</div>
              <div className="pDetail">{contact}</div>
            </div>
          }

          {falseQR ?
            <div>
              <img className="detailImage" src={image} />
              <h3 className="dTail">
                Details
                      </h3>
            </div>
            : ""}
          <br /><br />
          <Unverified data={notification} />

        </div>
      </div>
    )
  }
}

export default withRouter(History)
